@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
/* font-family: 'Playfair Display', serif;
font-family: 'Poppins', sans-serif; */

/* navbar */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: 0.5px solid gray !important;
}

.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 3px solid black;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", "san-serif";
  /* letter-spacing: 2px; */
}

/* footer */
.footer {
  color: #fff;
  padding: 25px;
  background: #000;
  /* fallback to old browsers */
  background: -webkit-linear-gradient(to right, #40717f, #000000, #40717f);
  /* chrome safari */
  background: linear-gradient(to right, #40717f, #000000, #40717f);
}

.footer a {
  text-decoration: none;
  color: #d1d1d1;
  margin: 10px;
}

.footer a:hover {
  color: aquamarine;
  font-weight: 500;
}

/* page not found */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: #000;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  height: 80vh;
  width: 100vw;
}

.contact-btn {
  padding: 0.25rem 8.5rem;
  margin: 0 0rem;
  margin-top: 6rem;
  margin-bottom: 1.5rem;
  font-size: 2rem;
  background-color: #000;
  color: #fff;
}

.footer-image {
  margin: 3rem;
  height: 30rem;
}

.content-container {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: larger;
  font-weight: 450;
  flex-direction: column;
}

